function setCookie() {
  var sess_token = $_GET('sess_token');
  if (sess_token) {
    if ($.cookie('ci_session')) {
      $.removeCookie('ci_session', { path: '/' });
    }

    var date = new Date();
    date.setTime(date.getTime() + 86400 * 1000); //sec秒  
    $.cookie('ci_session', sess_token, { path: '/', expires: date });
  }
}
/**
 * [$_GET 从url取参数值]
 * @param  {[type]} key [description]
 * @return {[type]}     [description]
 */
function $_GET(key) {
  var p = window.location.href.split('?'),
      v = '';
  if (p.length < 2) {
    return false;
  }

  p = p[1].split('&');
  p.map(function (item) {
    var temp = item.split('=');
    if (temp[0] == key) {
      if (temp.length > 2) {
        for (var i in temp) {
          if (i > 0) {
            if (i > 1) {
              v += '=' + temp[i];
            } else {
              v += temp[i];
            }
          }
        }
      } else {
        v = temp[1];
      }
    }
  });
  return v;
}

//判断页面是否加载完毕
function preLoading(id, fn) {
  document.onreadystatechange = function () {
    if (document.readyState == 'complete' || document.readyState == 'loaded') {
      finish(fn);
    }
  };
  function finish(fn) {
    setTimeout(function () {
      document.getElementById(id).style.display = 'none';
      if (fn) {
        fn();
      }
    }, 1000);
  }

  $('#' + id).on('touchstart', function () {
    return false;
  });
}
//模拟hover效果
function setHover(seletor, sClass, target) {

  if (!seletor) return;
  var sClass = sClass || 'hover';
  $(document).on('touchstart', seletor, function () {
    if (target == 'parent') {
      $(this).parent().addClass(sClass);
      hoverObj = { obj: $(this).parent(), class: sClass };
    } else if (target) {
      $(target).addClass(sClass);
      hoverObj = { obj: $(target), class: sClass };
    } else {
      $(this).addClass(sClass);
      hoverObj = { obj: $(this), class: sClass };
    }
  });

  var ua = window.navigator.userAgent;

  if (ua.indexOf('Android') != -1) {
    $(document).on('touchmove', seletor, function () {
      hoverObj.obj.removeClass(hoverObj.class);
    });
  }

  $(document).on('touchend', seletor, function () {
    hoverObj.obj.removeClass(hoverObj.class);
  });
}
//根据时间戳生成的时间对象
function changeTime(data1, type) {
  function todub(nun) {
    if (nun < 10) {
      nun = '0' + nun;
    }
    return nun;
  }

  var date = data1;
  date = date.substring(0, 19);
  date = date.replace(/-/g, '/');
  var timestamp = new Date(date).getTime();
  var d = new Date(timestamp);
  var data = '';
  console.log(date);
  if (type == '0') {
    data = d.getFullYear() + "年" + todub(d.getMonth() + 1) + "月" + todub(d.getDate()) + "日";
  } else if (type == '1') {
    data = todub(d.getMonth() + 1) + "月" + todub(d.getDate()) + "日" + todub(d.getHours()) + ":" + todub(d.getMinutes()) + ":" + todub(d.getSeconds());
  }
  return data;
}

