//ua参数获取
function getUaparen(key) {
  var p = window.navigator.userAgent;
  var v = '';
  p = p.split('&');
  p.map(function (item) {
    var temp = item.split('-');
    if (temp[0] == key) {
      if (temp.length > 2) {
        for (var i in temp) {
          if (i > 0) {
            if (i > 1) {
              v += '-' + temp[i];
            } else {
              v += temp[i];
            }
          }
        }
      } else {
        v = temp[1];
      }
    }
  });
  return v;
}

